/* Modern 2048 Game Styles */
@import url("https://fonts.googleapis.com/css2?family=Inter:wght@400;500;600;700;800&display=swap");

* {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  font-family: "Inter", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body {
  background: linear-gradient(135deg, #faf8ef 0%, #f5f2e8 100%);
  min-height: 100vh;
  transition: background 0.3s ease;
  position: relative;
  overflow-x: hidden;
}

body::before {
  content: "";
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: radial-gradient(
      circle at 20% 80%,
      rgba(120, 119, 198, 0.1) 0%,
      transparent 50%
    ),
    radial-gradient(
      circle at 80% 20%,
      rgba(255, 177, 153, 0.1) 0%,
      transparent 50%
    ),
    radial-gradient(
      circle at 40% 40%,
      rgba(120, 119, 198, 0.05) 0%,
      transparent 50%
    );
  pointer-events: none;
  z-index: -1;
}



/* Custom scrollbar */
::-webkit-scrollbar {
  width: 8px;
}

::-webkit-scrollbar-track {
  background: #f1f1f1;
  border-radius: 4px;
}

::-webkit-scrollbar-thumb {
  background: #bbada0;
  border-radius: 4px;
}

::-webkit-scrollbar-thumb:hover {
  background: #9f8a76;
}

/* Focus styles for accessibility */
button:focus-visible,
select:focus-visible {
  outline: 2px solid #8f7a66;
  outline-offset: 2px;
}

/* Prevent text selection on game elements */
.game-board,
.tile {
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

/* Mobile optimizations */
@media (max-width: 640px) {
  body {
    padding: 0;
  }

  .game-container {
    padding: 0.5rem;
  }

  /* Responsive layout adjustments */
  .main-container {
    padding: 0.5rem;
  }

  .game-title {
    font-size: 2.5rem !important;
  }

  .game-board {
    max-width: 95vw;
    max-height: 60vh;
  }

  .control-buttons {
    gap: 0.25rem;
  }

  .control-button {
    width: 2rem;
    height: 2rem;
    font-size: 0.875rem;
  }
}

/* Ensure viewport fit */
@media (max-height: 700px) {
  .game-title {
    font-size: 2rem !important;
  }
  
  .game-board {
    max-height: 50vh;
  }
}

/* High contrast mode support */
@media (prefers-contrast: high) {
  .tile {
    border: 2px solid #000;
  }
}

/* Tile animations */
.animate-tile-appear {
  animation: tile-appear 0.2s ease-out;
}

.animate-tile-merge {
  animation: tile-merge 0.3s ease-out;
}

@keyframes tile-appear {
  0% {
    opacity: 0;
    transform: scale(0);
  }
  100% {
    opacity: 1;
    transform: scale(1);
  }
}

@keyframes tile-merge {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.1);
  }
  100% {
    transform: scale(1);
  }
}

/* Reduced motion support */
@media (prefers-reduced-motion: reduce) {
  * {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
  }
  
  .tile-appear-active,
  .tile-enter-active,
  .tile-exit-active {
    transition-duration: 0.01ms !important;
  }
}
